.page {
  page-break-after: always;
  position: relative;
}
.page .form-img {
  width: 100%;
}

.page-details.quincy {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10000;
}
.page-details.quincy input[type=date]::-webkit-inner-spin-button, .page-details.quincy input[type=date]::-webkit-calendar-picker-indicator,
.page-details.quincy input[type=year]::-webkit-inner-spin-button,
.page-details.quincy input[type=year]::-webkit-calendar-picker-indicator,
.page-details.quincy input[type=month]::-webkit-inner-spin-button,
.page-details.quincy input[type=month]::-webkit-calendar-picker-indicator {
  display: none;
  -webkit-appearance: none;
}
.page-details.quincy .form-img {
  width: 100%;
}
.page-details.quincy .answer {
  position: absolute;
  bottom: 0;
}
.page-details.quincy .answer img {
  width: 100%;
  position: absolute;
}
.page-details.quincy .answer input,
.page-details.quincy .answer textarea {
  width: 100%;
  font-size: 10px;
  font-weight: bold;
  color: #141414;
  position: absolute;
  background: transparent;
  overflow: hidden;
}
.page-details.quincy .answer textarea {
  font-size: 10px;
  padding: 0 5px;
}
.page-details.quincy .answer.monospace {
  font-family: monospace;
  letter-spacing: 7px;
}
.page-details.quincy .answer .check-mark {
  position: absolute;
}
.page-details.quincy .answer .check-mark::before, .page-details.quincy .answer .check-mark::after {
  content: "";
  height: 2px;
  width: 15px;
  background: black;
  position: absolute;
  transform: rotate(45deg);
}
.page-details.quincy .answer .check-mark::before {
  transform: rotate(-45deg);
}
.page-details.quincy textarea.form-control {
  border: 0;
  resize: none;
  height: 100%;
}
